@import url(https://fonts.googleapis.com/css?family=Inconsolata&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.greeting {
    grid-column-start: 3;
    grid-column-end: 4;
    grid-row-start: 2;
    grid-row-end: 3;
}
.name {
    font-size: 60px;
    color:white;
    font-family: sans-serif;
    font-family: 'Roboto';
}

.headline {
    margin: 0px 0px 20px 3px;
    font-size: 20px;
    font-family: 'Inconsolata';
    color: #ff6768;
}

.bottom {
    margin-top: 10px;
}
.top {
    margin-bottom: 0px;
}
.limit {
    max-width: 500px;
}
.tagline {
    color: grey
}

.social-bar {
    grid-column-start: 5;
    grid-column-end: 6;
    grid-row-start: 2;
    grid-row-end: 4;
    min-width:150px;
}

.icons {
    margin-left: 5vw;
    position:fixed;
}

.icon {
    color:grey;
    padding-bottom:25px;
}
.icon:hover {
    color: #ff6768;
}

.navbar {
    grid-column: 3 / 4;
    grid-row: 1 / 2;
}
.navbar-elements {
    float:right;
}
.nav-items {
    font-family: 'Inconsolata';
    list-style-type: none;
    display:inline-block;
    margin-top: 20px;
    margin-left:30px;
    padding: 0;
    overflow: hidden;
}

.nav-items:hover {
    color:#ff6768;
    -webkit-text-decoration-style: underline;
            text-decoration-style: underline;
    cursor:pointer;
}

.nav-list {
    color:white;
}
.highlight-color {
    color: #ff6768;
}

a {
    color:inherit;
    text-decoration: none;
}

.resume-link {
    padding: 0;
    margin: 0;
    border: 3px;
    border-style:dashed;
}
.about-container {
    display:inline-grid;
    grid-template-columns: 60% 40%;
}

.descripton {
    grid-column-start: 1;
    grid-column-end: 2;
    max-width: 600px;
}

.text {
    color: grey;
    font-size: 15px;
    line-height: 1.75em;
}

.technology-list {
    columns: 2;
    -webkit-columns: 2;
    -moz-columns: 2;
    -webkit-padding-start: 1em;
            padding-inline-start: 1em;
    font-family: 'Inconsolata';
    list-style:none;
}

li {
    list-style-type: none;
}

.technology-list > li:before {
    content: "";
    border-color: transparent #ff6768;
    border-style: solid;
    border-width: 0.35em 0 0.35em 0.45em;
    display: block;
    height: 0;
    width: 0;
    left: -1em;
    top: 1.2em;
    position: relative;
}
.image-container {
    float:right;
    width:100%;
    max-width: 300px;
}
.center-grid {
  grid-column: 2/3;
  grid-row: 2/3;
}

ol {
  padding: 0;
  margin: 0;
}

.tabs {
  display: flex;
  flex-direction: row;
}

.tab-content {
    width: 100%;
}

.tab-list {
    color:rgb(96, 106, 134);
}

.tab-list-item:hover {
  color: #ff6768;
  background-color: rgba(255, 103, 104, 0.3);
}

.tab-list-item {
  list-style: none;
  padding: 0.5rem 0.75rem;
  border-left: 2px solid rgb(51, 63, 88);
}

.tab-list-active {
  color: #ff6768;
  background-color: rgba(255, 103, 104, 0.3);
  border-left: 2px solid #ff6768;
}

table {
    width: 100%;
}

.cell-menu {
    width: 200px;
}

.experience-title {
    margin: 0;
    font-family: 'Roboto'
}

.experience-subtitle {
    font-family: 'Roboto';
    margin: 0;
    margin-top:10px;
}

.experience-container {
    margin-left: 10%;
    color: white;
}
.experience-content > li:before {
    content: "";
    border-color: transparent #ff6768;
    border-style: solid;
    border-width: 0.35em 0 0.35em 0.45em;
    display: block;
    height: 0;
    width: 0;
    left: -1em;
    top: 0.9em;
    position: relative;
}

.experience-content {
    padding-left: 1em;
}
.tile-holder {
  display: flex;
  flex-flow: row wrap;
  grid-column: 3 / 4;
  grid-row: 2 / 3;
}

.tile-holder > div {
  background-color: #263859;
  color: white;
  width: 100px;
  margin-right:20px;
  margin-bottom: 20px;
  line-height: 45px;
}


.tile-container:hover {
  -webkit-transform: translate(0px, -5px);
          transform: translate(0px, -5px)
}


.projects-title {
    align-self: end;
    grid-column: 3 / 4;
    grid-row: 1 / 2;
}

.contact {
  display: table;
  margin-top: 50px;
  height: 200px;
  width: 100%;
  background-color: rgb(2, 12, 27);
  text-align: center;
}

.contact-text {
  color:white;
  font-size: 20px;
  font-family: 'Roboto'
}

.inner-text {
  display: table-cell;
  vertical-align: middle; 
  padding-left: 25vw;
  padding-right: 25vw;
}

.link-text {
  color: #d13845;
  text-decoration: underline;
}
h2 {
    margin: 0;
    padding: 0;
    padding-left: 15px;
    font-family: 'Roboto'
}

.tile-container {
    margin-left:0px;
    min-width: 300px;
}

.tile-description {
    padding-left: 15px;
    min-height: 200px;
}

.tile-description > p {
    word-spacing: 0.1em
}

.tile-container > header {
    display:flex;
    justify-content: space-between;
}

.tag {
    float:left;
    color:grey;
    font-size: 20px;
    font-family: 'Inconsolata';
    padding-left:15px;
}


html {
  scroll-behavior: smooth;
}

.App {
  margin: 0;
}
body {
  background-color: #17223b;
}

.section {
  width: auto;
}

.about {
  display: grid;
  grid-template-columns: 10vw 50px auto 50px 10vw;
  grid-template-rows: 30vh auto 40vh;
} 

.personal {
  display: grid;
  grid-template-columns: 10vw 50px auto 50px 10vw;
  grid-template-rows: 10vh auto;
}

.experience {
  display: grid;
  grid-template-columns: 20vw auto 20vw;
  grid-template-rows: 25vh auto 25vh;
}

.projects {
  display: grid;
  grid-template-columns: 10vw 50px auto 50px 10vw;
  grid-template-rows: 15vh auto;
}

h4 {
  position:fixed;
}

.center-item {
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 2;
  grid-row-end: 3;
}

.sectionHeading {
  font-family: 'Inconsolata';
  color: #ff6768;
}

.sectionInside {
  color:white;
  font-family: 'Roboto'
}

.center-end {
  grid-column: 1 / 6;
  grid-row: 3 / 4;
  background-color: #2F2F2F;
}

